<template>
  <div class="">
    <div class="">
      <div class="_">
        <img src="@/assets/img_dayCircle_circle.png" />
        <div><span class=""></span> ( {{ number }})</div>
      </div>
      <div class="_" >
        <div v-for="(schedule, index) in week" :key="index">
          <div
            :style="{
              backgroundColor: schedulesTemp.includes(index)
                ? category.color
                : '#fff',
            }"
            :class="schedulesTemp.includes(index) ? '_' : '_'"
            class=""
            @click="ButtonDay(index)"
          >
            {{ schedule }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    schedules: {
      type: Array,
    }, 
    category: {
      type: Object,
      required: true
    }},
  data() {
    return {
      week: ["", "", "", "", "", "", ""],
      schedulesTemp: this.schedules,
    };
  },
  methods: {
    ButtonDay(ind) {
      if (this.schedulesTemp.includes(ind)) {
        this.schedulesTemp = this.schedulesTemp.filter((item) => item !== ind);
        this.schedulesTemp.sort();
      } else {
        this.schedulesTemp = [...this.schedulesTemp, ind];
        this.schedulesTemp.sort();
      }
      this.$emit("editSchedule", this.schedulesTemp);
      this.$forceUpdate();
    },
  },
  computed: {
    number() {
      return this.schedulesTemp.length;
    },
  },
};
</script>

<style scoped>
. {
  padding: 0px 5px;
}
. {
  display: flex;
  flex-flow: column;
  justify-content: center;
  gap: 20px;
  width: 100%;
  height: 140px;
  background-color: #ffffff;
  border-radius: 15px;
}
img {
  width: 20px;
  height: 20px;
}
. {
  font-weight: bold;
}
._ {
  display: flex;
  align-items: center;
  gap: 16px;
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
  font-size: 16px;
  color: #000000;
}

._ {
  display: flex;
  justify-content: space-around;
}
. {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  border-radius: 100%;
  font-size: 14px;
  color: #000;
  cursor: pointer;
  transition: all 0.15s;
}
._ {
  color: #ffffff;
}
._ {
  border: 1px solid #dedede;
}
</style>