<template>
  <div class="">
    <div class="" v-if="getData">
      <div class="">
        <TheHabitHeader
          :habitName="habitItem.objective"
          :category="habitCategory"
          @editHabitName="editHabitName"
        />
        <TheSelectSchedule
          :schedules="habitItem.schedule"
          :category="habitCategory"
          @editSchedule="editSchedule"
        />
        <TheHabitDetailList
          :detailHabitItems="detailHabitItems"
          :category="habitCategory"
          @addDetailHabitItem="addDetailHabitItem"
          @editDetailHabitItem="editDetailHabitItem"
          @deleteDetailHabitItem="deleteDetailHabitItem"
        />
      </div>
      <div class="_" v-if="$route.params.id && !confirm" >
        <div class="_" @click="confirm = !confirm"></div>
        <div class="_" @click="EditSave" :style="{ backgroundColor: habitCategory.color }"></div>
      </div>
      <div class="_" v-if="$route.params.category && !confirm">
        <div class="_" @click="EditSave" :style="{ backgroundColor: habitCategory.color }" ></div>
      </div>

      <base-modal v-if="confirm" >
        <template v-slot:header>
          <div class="_">
            <img v-if="habitItem.objective && habitItem.schedule.length > 0 && $route.params.category" 
            src="@/assets/confetti.png" alt="">
            {{ modalHeader }}
            <img v-if="habitItem.objective && habitItem.schedule.length > 0 && $route.params.category" 
            src="@/assets/confetti.png" alt="">
            </div>
        </template>
        <template v-slot:contents>
          <div class="_">
            <p>{{ modalContents[0] }}</p>
            <p>{{ modalContents[1] }}.</p>
          </div>
        </template>
        <template v-slot:confirm>
          <div class="_" v-if="$route.params.id">
            <div class="_" @click="deleteObjective"></div>
            <div class="_" @click="confirm = !confirm"></div>
          </div>
          <div class="_" v-if="$route.params.category">
            <div
            :style="{ backgroundColor: habitCategory.color }"
            @click="habitItem.objective && habitItem.schedule.length > 0 ? $router.go(-2) : confirm = !confirm">
              
            </div>
          </div>
        </template>
      </base-modal>
    </div>
  </div>
</template>

<script>
import TheHabitHeader from "@/components/TheHabitHeader.vue";
import TheSelectSchedule from "@/components/TheSelectSchedule.vue";
import TheHabitDetailList from "@/components/TheHabitDetailList.vue";
import BaseModal from "@/components/BaseModal.vue";

export default {
  props: {
    id: {
      type: String
    },
    category: {
      type: String,
    }
  },
  components: {
    TheHabitHeader,
    TheSelectSchedule,
    TheHabitDetailList,
    BaseModal,
  },
  data() {
    return {
      habitItem: {
        schedule: [],
        objective: "",
        category: this.category,
      },
      detailHabitItems: [],
      habitCategory: {},
      confirm: false,
      getData: this.id ? false : true
    }
  },
  computed: {
    modalHeader() {
      if (this.id) {
        return "  ?"
      } else if ( this.habitItem.objective && this.habitItem.schedule.length > 0 ) {
        return "  !"
      } else {
        return "  ."
      }
    },
    modalContents() {
      if (this.id) {
        return ["    ", "   ."]
      } else if ( this.habitItem.objective && this.habitItem.schedule.length > 0 ) {
        return ["  .", "  ."]
      } else {
        return ["   .", "  ."]
      }
    },
  },
  methods: {
    editHabitName(newValue) {
      this.habitItem.objective = newValue;
      // console.log(this.habitItem.objective)
    },
    editSchedule(newValue) {
      this.habitItem.schedule = newValue;
      // console.log(this.habitItem.schedule)
    },
    addDetailHabitItem() {
      this.detailHabitItems.push({objective: ""});
      // console.log(this.detailHabitItems)
    },
    editDetailHabitItem(ind, newValue) {
      this.detailHabitItems[ind].objective = newValue;
      // console.log(this.detailHabitItems[ind])
    },

    //  
    async getHabitItems() {
      try {
        let { data } = await this.axios({
          method: "get",
          url: `/api/objectives/${this.id}`,
        });
        // console.log(data);
        this.habitItem = data;
        this.habitCategory = this.$store.state.habitCategory[data.category];
        this.getData = true
      } catch (err) {
        console.log(err);
      }
    },
    //  
    async getDetailHabitItems() {
      try {
        let { data } = await this.axios({
          method: "get",
          url: `/api/detailedObjectives/${this.id}`,
        });
        // console.log(data);
        this.detailHabitItems = data;
      } catch (err) {
        console.log(err);
      }
    },

    //  
    async editHabit() {
      try {
        let { data } = await this.axios({
          method: this.id ? "put" : "post",
          url: this.id ? `/api/objectives/${this.id}` : "/api/objectives",
          data: { ...this.habitItem },
        });
        // console.log(data)
        if (this.detailHabitItems.length > 0) {
          this.detailHabitItems.forEach(async (el) => {
            if(el.objective.length > 0) {
              if (el.id) {
                await this.editDetailHabit(el.id, el.objective);
              } else {
                await this.createDetailHabit(data.id, el.objective);
              }
            }
          });
          this.confirm = !this.confirm;
        } else {
          this.confirm = !this.confirm;
        }
      } catch (err) {
        console.log(err);
      }
    },
    //  
    async createDetailHabit(objectiveId, newDetailHabit) {
      try {
        await this.axios({
          method: "post",
          url: `/api/detailedObjectives/${objectiveId}`,
          data: {
            objective: newDetailHabit,
          },
        });
      } catch (err) {
        console.log(err);
      }
    },
    //  
    async editDetailHabit(id, editDetailHabit) {
      try {
        await this.axios({
          method: "put",
          url: `/api/detailedObjectives/${id}`,
          data: {
            objective: editDetailHabit,
          },
        });
      } catch (err) {
        console.log(err);
      }
    },

    //  
    async deleteDetailHabitItem(ind) {
      if (this.detailHabitItems[ind].id) {
        try {
          await this.axios({
            method: "DELETE",
            url: `/api/detailedObjectives/${this.detailHabitItems[ind].id}`,
          });
        } catch (err) {
          console.log(err);
        }
        this.detailHabitItems.splice(ind, 1);
      } else {
        this.detailHabitItems.splice(ind, 1);
      }
    },

    //  
    async EditSave() {
      if(this.habitItem.objective && this.habitItem.schedule.length > 0) {
        if (this.id) {
          await this.editHabit();
          this.$router.go(-1);
        } else {
          await this.editHabit();
        }
      } else {
        this.confirm = !this.confirm
      }
    },
    //  
    async deleteObjective() {
      try {
        await this.axios({
          method: 'delete',
          url: `/api/objective/${this.$route.params.id}`,
        })
        // console.log('')
        this.$router.go(-1)
      }
      catch (err) {
        console.log(err.data);
      } 
    },
  },
  async created() {
    if (this.id) {
      await this.getHabitItems();

      await this.getDetailHabitItems();
    } else {
      this.habitCategory = this.$store.state.habitCategory[this.category];
    }
  },
};
</script>

<style scoped>
. {
  justify-content: space-between;
  width: 100%;
  height: 100vh;
  background-color: #e1e1e1;
  overflow: hidden;
}
. {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
/*  */
._ {
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  text-align: center;
  color: #ffffff;
}
._ div {
  cursor: pointer;
}
._ {
  width: 50%;
  background: #ff5757;
}
._ {
  width: 50%;
}
._ {
  width: 100%;
}
/*  */
._ {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
}
._ img{
  width: 25px;
  height: 25px;
}
._ {
  font-size: 16px;
}
._ {
  display: flex;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40px;
  line-height: 40px;
}
._ div {
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  cursor: pointer;
}
._ {
  background-color: #5e5e5e;
}
._ {
  font-size: 20px;
  font-weight: bold;
}
</style>