<template>
  <div class="" :style="{ backgroundColor: category.color }">
    <div class="">
      <div class="_" @click="previousPage">
        <img src="@/assets/Previous-white.png" alt="" />
      </div>
      <div>
        {{ $route.params.id ? " " : " " }}
      </div>
    </div>
    <div class="_">
      <div class="">
        <div class="" :style="{ color: category.color }">
          {{ category.name }}
        </div>
        <div class="_">
          <div class="" 
          :contenteditable="true"
          @input="inputHabitName" 
          @focus="placeholder = ''" 
          @blur="placeholder = ' .'">
            {{
              habitName ? habitName : placeholder
            }}
          </div>
          <div class="__">
            <img src="@/assets/img_edit_text.png" />
          </div>
        </div>
      </div>
      <div class="_" v-if="category && category.imgUrl">
        <img class="_" :src="require(`@/assets/habitIcon/img_habit_${category.imgUrl}.png`)" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    habitName: {
      type: String,
    }, 
    category: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      placeholder: " .",
    }
  },
  methods: {
    previousPage() {
      this.$router.go(-1);
    },
    inputHabitName(event) {
      let newHabit = event.currentTarget.innerText
      if(newHabit.length > 11) {
        alert('  .')
        newHabit = newHabit.substring(0, 11)
      }
      // console.log(newHabit)
      this.$emit('editHabitName', newHabit);
    }
  },
  
};
</script>

