<template>
  <header>
    <div class="">
      <div class=""/>
      <div class="">
        <img src="@/assets/logo.png" alt="" />
      </div>
      <div class="" v-if="!user">
        <img
          src="../assets/kakao_login_small.png"
          alt="kakao_login_button"
          class="login_btn"
          @click="kakaoLogin"
        />
      </div>
      <div class="" @click="logout" v-else>
        <div></div>
      </div>
    </div>
    <div class="" v-if="user">
      <router-link to="/total" >
        <div :class="{ '': $route.path == '/total' }">
          
        </div>
      </router-link>
      <router-link to="/today" >
        <div :class="{ '': $route.path == '/today' }">
          
        </div>
      </router-link>
      <router-link to="/myinfo" >
        <div :class="{ '': $route.path == '/myinfo' }">
          
        </div>
      </router-link>
    </div>
  </header>
</template>

<script>
export default {
  computed: {
    user() {
      return this.$store.state.user;
    },
  },
  methods: {
    kakaoLogin() {
      window.Kakao.init(process.env.VUE_APP_JAVASCRIPT_KEY);

      // console.log(window.Kakao.isInitialized());

      window.Kakao.Auth.authorize({
        redirectUri: "http://ec2-52-79-218-70.ap-northeast-2.compute.amazonaws.com/loading",
      });
    },
    async logout() {
      try {
        await this.axios({
          method: "delete",
          url: "/api/auth",
        });
        location.href = "/";
      } catch (err) {
        console.log(err.data);
      }
    },
  },
  props: ["isActived", "notActived"],
};
</script>

<style lang="css" scoped>
header {
  display: flex;
  flex-direction: column;
  width: 100%;
}
. {
  opacity: 0;
  width: 60px;
  height: 30px;
}
. {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px 0px;
}
. div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 40px;
}
img {
  width: auto;
  max-height: 40px;
}
.,
. {
  cursor: pointer;
}
. div {
  width: 60px;
  height: 30px;
  line-height: 30px;
  font-size: 12px;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  background-color: #777;
}
. {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 40px;
  border-bottom: 2px solid #ddd;
}
. div {
  width: 100px;
  font-size: 20px;
  font-weight: bold;
  line-height: 40px;
  text-align: center;
}
. a {
  color: #9b9b9b;
}
. {
  border-bottom: 4px solid #000;
  color: #2c3e50;
  text-align: center;
} 

</style>